/* Global Reset and Box Sizing */
*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 1rem; /* Padding for mobile screens */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f9f9f9;
  color: #333;
}

/* Typography */
h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.75rem;
}

p {
  font-size: 1rem;
  line-height: 1.6;
}

@media (max-width: 768px) {
  h1 {
    font-size: 1.5rem;
  }

  h2 {
    font-size: 1.25rem;
  }

  p {
    font-size: 0.9rem;
  }
}

/* Links */
a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Navigation */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: #007bff;
  color: white;
}

.navbar a {
  color: white;
  text-decoration: none;
}

.hamburger-menu {
  display: none;
  font-size: 1.5rem;
}

@media (max-width: 768px) {
  .hamburger-menu {
    display: block;
  }

  .navbar-links {
    display: none;
  }

  .navbar-links.active {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }
}

/* Skills Grid */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 1rem;
}

@media (max-width: 480px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }
}

/* Buttons */
button {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  background: #007bff;
  color: white;
  cursor: pointer;
}

button:hover {
  background: #0056b3;
}

@media (max-width: 480px) {
  button {
    width: 100%;
  }
}

/* Images */
img {
  max-width: 100%;
  height: auto;
}

iframe {
  width: 100%;
  height: auto;
}